import { graphql } from "gatsby";
import React from "react";
import SEO from "../components/misc/seo";
import { Layout } from "../components/structure/layout";
import { BlogArticle } from "../components/widgets/blog-article";
const BlogArticleTemplate = ({ data: { markdownRemark, site }, }) => {
    const { frontmatter, html } = markdownRemark;
    return (<Layout>
      <SEO title="Home"/>
      <BlogArticle author={frontmatter.author} baseUrl={site.siteMetadata.baseUrl} date={frontmatter.date} htmlContent={html} path={frontmatter.path} title={frontmatter.title}/>
    </Layout>);
};
export default BlogArticleTemplate;
export const pageQuery = graphql `
  query getBlogArticle($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        author
        date(formatString: "MMMM DD, YYYY")
        path
        title
      }
    }
    site {
      siteMetadata {
        baseUrl
      }
    }
  }
`;
